"use client";

import React, { useState, useRef, useEffect } from "react";
import { Briefcase, Building2, Rocket, ArrowLeft } from "lucide-react";
import { gsap } from "gsap";

const SignUpPage = () => {
  const [businessType, setBusinessType] = useState(null);
  const [showBusinessForm, setShowBusinessForm] = useState(false);
  const businessFormRef = useRef(null);

  const businessOptions = [
    { label: "Freelancer", icon: <Briefcase className="w-5 h-5" /> },
    { label: "SME", icon: <Building2 className="w-5 h-5" /> },
    { label: "Startup", icon: <Rocket className="w-5 h-5" /> },
  ];

  useEffect(() => {
    if (showBusinessForm) {
      gsap.fromTo(
        businessFormRef.current,
        { x: '100%', opacity: 0 },
        { x: '0%', opacity: 1, duration: 0.5, ease: 'power3.out' }
      );
    } else {
      gsap.to(businessFormRef.current, {
        x: '100%', opacity: 0, duration: 0.5, ease: 'power3.in'
      });
    }
  }, [showBusinessForm]);

  const handleBusinessClick = (type) => {
    setBusinessType(type);
    if (type !== "Freelancer") {
      setShowBusinessForm(true);
    } else {
      setShowBusinessForm(false);
    }
  };

  const handleBackClick = () => {
    setShowBusinessForm(false);
    setBusinessType(null);
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-[#F4F7FA] p-6">
      <div className="w-full max-w-4xl bg-white shadow-md rounded-2xl border border-[#E1E8F5] overflow-hidden flex flex-col md:flex-row">
        {/* Sidebar */}
        <div className="hidden md:flex flex-col justify-center items-center w-1/3 bg-[#1B2A49] text-white p-8">
          <h2 className="text-3xl font-bold mb-4">Welcome</h2>
          <p className="text-center text-sm leading-relaxed">
            Create your account and grow your business with us.
          </p>
        </div>

        {/* Main Form */}
        <div className="flex-1 p-8 relative overflow-hidden">
          <h1 className="text-2xl font-bold text-[#1B2A49] mb-6">Sign Up</h1>

          {/* Base Form */}
          {!showBusinessForm && (
            <form className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <input type="text" placeholder="Full Name" className="w-full border border-[#E1E8F5] rounded-lg px-4 py-2 text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4]" />
                <input type="email" placeholder="Email" className="w-full border border-[#E1E8F5] rounded-lg px-4 py-2 text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4]" />
                <input type="tel" placeholder="Phone Number" className="w-full border border-[#E1E8F5] rounded-lg px-4 py-2 text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4]" />
                <input type="password" placeholder="Password" className="w-full border border-[#E1E8F5] rounded-lg px-4 py-2 text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4]" />
              </div>

              <div>
                <label className="block text-[#1B2A49] font-semibold mb-4">Choose Business Type</label>
                <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
                  {businessOptions.map(({ label, icon }) => (
                    <div key={label} onClick={() => handleBusinessClick(label)} className={`flex flex-col items-center justify-center border rounded-xl p-4 cursor-pointer transition shadow-sm hover:shadow-md ${businessType === label ? "border-[#1B2A49] bg-[#F4F7FA]" : "border-[#E1E8F5] bg-white"}`}>
                      <div className="text-[#1B2A49] mb-2">{icon}</div>
                      <span className="text-[#344767] font-medium">{label}</span>
                    </div>
                  ))}
                </div>
              </div>

              <div className="flex mt-6">
                <button type="submit" className="w-full bg-[#1B2A49] text-white py-2 px-4 rounded-lg shadow hover:bg-[#162038] transition">Sign Up</button>
              </div>
            </form>
          )}

          {/* Business Details Slide Form */}
          {showBusinessForm && (
            <div ref={businessFormRef} className="absolute top-0 left-0 w-full h-full bg-white p-6">
              <div className="flex items-center mb-6 cursor-pointer" onClick={handleBackClick}>
                <ArrowLeft className="w-5 h-5 mr-2 text-[#1B2A49]" />
                <span className="text-[#1B2A49] font-semibold">Back</span>
              </div>

              <h2 className="text-xl font-semibold text-[#1B2A49] mb-4">Business Setup Information ({businessType})</h2>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <input type="text" placeholder="Company Name" className="w-full border border-[#E1E8F5] rounded-lg px-4 py-2 text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4]" />
                <input type="text" placeholder="Trade License No." className="w-full border border-[#E1E8F5] rounded-lg px-4 py-2 text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4]" />
                <input type="text" placeholder="VAT ID" className="w-full border border-[#E1E8F5] rounded-lg px-4 py-2 text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4]" />
                <input type="text" placeholder="Industry" className="w-full border border-[#E1E8F5] rounded-lg px-4 py-2 text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4]" />
                <select className="w-full border border-[#E1E8F5] rounded-lg px-4 py-2 text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4]">
                  <option value="English">English</option>
                  <option value="Arabic">Arabic</option>
                </select>
              </div>

              <div className="flex mt-6">
                <button type="submit" className="w-full bg-[#1B2A49] text-white py-2 px-4 rounded-lg shadow hover:bg-[#162038] transition">Sign Up</button>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default SignUpPage;
